*,*:before, *:after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container {
    min-width: 0;
    min-height: 0;
    width: auto;
    height: auto;
    position: absolute;
    left: 50%;
    top: 50%;
    padding: 0px;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    box-shadow: 0px 4px 20px 1px rgba(0,0,0,.2);
    display: grid;
    grid-template: auto 1fr / repeat(3, 1fr);
    grid-template-areas: 'digit digit digit'
                        'increment reset decrement';
    
}
.container > * {
    overflow: hidden;
}

h1 {
    grid-area: digit;
    text-align: center;
    font-size: 45px;
    font-weight: 900;
    justify-self: center;
    align-self: center;
    padding: 10px 0px 10px 0px;
    margin-bottom: 5px;
    color: rgba(55,153,255,1);
}
button {
    font-size: 24px;
    font-weight: 600;
    border: none;
    padding: 20px 16px;
    cursor: pointer;
    background:rgba(31,143,255,1);
    outline: none;
    color: rgba(255,255,255,.7);

}
button:hover {
    color: rgba(255,255,255,1);
}
.increment {
    grid-area: increment;
}
.decrement {
    grid-area: decrement;
}
.reset {
    grid-area: reset;
    color: rgba(92,184,92,1);
    background: whitesmoke;
}
.reset:hover {
    color: rgb(96, 218, 96);
}
